.home-container {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #DDD4ED;
  padding: 0 15%;
  padding-top: 2rem;
  min-height: 100vh; /* Updated property */
  min-width: 100vw;
  border: 1.3vh solid #443069;
  box-sizing: border-box;
}


.left-section {
  /* flex: 1; */
  text-align: left;
  animation: drop-in 1s ease 100ms backwards;
  padding-right: 2vw;
}

.right-section {
  /* flex: 1; */
  text-align: center;
  animation: drop-in 1s ease 100ms backwards;
  padding-left: 2vw;
}

h1 {
  font-size: 4rem;
  font-weight: 900;
  font-family: 'Playfair Display', serif;
  margin-bottom: 1rem;
  color: #362456;
}

p {
  font-size: 1.5rem;
  color: #362456;
  font-family: 'Playfair Display', serif;
  font-weight: 500;
}

.website-max {
  width: 100%; /* Adjust the width as per your preference */
  max-width: 500px;
  height: auto; /* Maintain the aspect ratio */
  border-radius: 50%;
  border-style: dashed;
  border-color: #362456;
}

@keyframes drop-in {
  from {
    opacity: 0;
    transform: translateY(-100px);
  }
  to {
    opacity: 1;
    transform: translate(0px);
  }
}

@media (max-width: 541px) {
  .home-container {
    flex-direction: column;
    padding: 10vh;
  }

  .left-section {
    padding-bottom: 5vh;
    padding-top: 5vh;
  }
}

/* Media Query for screens smaller than 600px */
@media (max-width: 600px) {
  .home-container {
    flex-direction: column;
    padding: 3vh;
  }

  .left-section {
    padding-top: 0;
    padding-bottom: 5vh;
  }

  .left-section,
  .right-section {
    text-align: center;
  }

  .website-max {
    max-width: 100%;
  }
}

@media (min-width: 600px) {
  .home-container {
    padding-top: 50vh;
    padding-bottom: 50vh;
  }
}

@media (min-width: 768px) {
  .home-container {
    min-width: 100vw;
    min-height: 100vh;
  }
  
}

