.about-me {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: left;
  padding-top: 10%;
  padding-right: 15vw;
  padding-bottom: 5%;
  padding-left: 15vw;
  box-sizing: border-box;
  min-height: 100vh;
  width: 100vw;
  background-color: #ddd4ed;
  border: 1.3vh solid #443069;
}

.about-me h2 {
  font-family: "Playfair Display", serif;
  text-align: left;
  font-weight: 700;
  color: #362456;
  font-size: 5vw;
  margin-bottom: 0;
  animation: drop-in 1s ease 100ms backwards;
}

.content p {
  display: flex;
  margin-top: 2vw;
  color: #362456;
  animation: drop-in 1s ease 100ms backwards;
  font-size: 1.5vw;
  font-family: Georgia, Cambria, Times New Roman, Times, serif;
  line-height: 1.5;
}

@media (max-width: 1180px) { /*tablet*/
  .about-me {
    padding-top: 15%;
    padding-right: 12%;
    padding-bottom: 8%;
    padding-left: 12%;
  }

  .about-me h2 {
    font-size: 5vw;
  }

  .content p {
    font-size: 2.5vw;
  }
}


@media (max-width: 768px) { /*ipad mini*/
  .about-me h2 {
    font-size: 10vw;
  }

  .content p {
    font-size: 4vw;
  }
}

@media (max-width: 480px) {
  .about-me {
    padding-top: 20%;
    padding-right: 10%;
    padding-bottom: 5%;
    padding-left: 10%;
  }

  .about-me h2 {
    font-size: 14vw;
  }

  .content p {
    font-size: 5vw;
  }
}
